/* eslint-disable eqeqeq */
import React, { useEffect, useLayoutEffect, useRef, useState } from "react";
import {
  Breadcrumbs,
  Button,
  Container,
  Typography,
  Box,
  Snackbar,
  Paper,
} from "@mui/material";
import { Redirect, useHistory } from "react-router";
import { makeStyles } from "@mui/styles";
import { Link, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  getByIdServiceDetail,
  getCheck,
  setPaymentCheck,
  setPaymentData,
} from "../../../../redux/reducers/pay24";
import FirstStep from "./FirstStep";
import StepperMain from "./Stepper";
import Reload from "../../../../components/Reload";
import { Alert } from "@mui/material";
import SecondStep from "./SecondStep";
import ThirdStep from "./ThirdStep";
import { Form, Formik } from "formik";
import Balance from "../../../../components/Balance";

const Pay24Detail = ({ t, main }) => {
  const classes = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();

  const steps = ["1", "2", "3"];
  const [activeStep, setActiveStep] = useState(0);
  const [skipped, setSkipped] = useState(new Set());
  const [loading, setLoading] = useState(false);

  const { detailsId } = useParams();

  const firstUpdate = useRef(true);

  const { ServicesDetail, check, name, isDetailLoading, payment } = useSelector(
    ({ Pay24Reducer }) => Pay24Reducer
  );

  // const { selectedCurrency } = useSelector(({ MainReducer }) => MainReducer);

  const { profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  const data =
    ServicesDetail?.data && JSON.parse(ServicesDetail?.data).provider;
  const dataContent = data?.pages?.page?.controls?.control[0];

  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  const handleNext = () => {
    let newSkipped = skipped;
    if (isStepSkipped(activeStep)) {
      newSkipped = new Set(newSkipped.values());
      newSkipped.delete(activeStep);
    }

    if (activeStep === 1) setActiveStep(2);

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped(newSkipped);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  useEffect(() => {
    dispatch(getByIdServiceDetail(detailsId));
    // eslint-disable-next-line
  }, []);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (check?.status == 200) {
      handleNext();
      setLoading(false);
    } else {
      setLoading(false);
    }
    // eslint-disable-next-line
  }, [check]);

  // let regexp = /^[?!,.а-яА-ЯёЁ]+$/;

  useEffect(() => {
    if (main) {
      if (data) document.title = `Binance Pay | ${data && data["@name"]}`;
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  return (
    <>
      {!main &&
      profileData?.data &&
      profileData?.data?.profile?.verification?.value != 2 ? (
        <Redirect exact from="/" to="/dashboard/settings/verification" />
      ) : ServicesDetail?.active === "1" ? (
        <Redirect exact from="/" to="/dashboard/payment-for-services/" />
      ) : (
        <Paper
          component="section"
          sx={{
            pt: main && 4,
            pb: main && 4,
            minHeight: main ? "100vh" : "66vh",
          }}
          className={classes.section}
        >
          {loading && <Reload />}
          <Snackbar
            open={
              (check?.data?.result && check.status != 200) ||
              (payment?.data?.result && payment.status != 200)
            }
            autoHideDuration={3000}
            onClose={() => {
              if (check?.data?.result && check.status != 200)
                dispatch(setPaymentCheck(null));
              else dispatch(setPaymentData(null));
            }}
            anchorOrigin={{ vertical: "top", horizontal: "center" }}
          >
            <Alert severity="error" color="error" variant="filled">
              {payment?.data?.result || check?.data?.result}
            </Alert>
          </Snackbar>
          <Container>
            <Button
              style={{ marginBottom: 16, display: "block" }}
              variant="contained"
              size="large"
              onClick={() => {
                if (activeStep === 0) history.goBack();
                else if (activeStep === 3)
                  setActiveStep((prevActiveStep) => prevActiveStep - 2);
                else setActiveStep((prevActiveStep) => prevActiveStep - 1);
              }}
            >
              {t("GoBack")}
            </Button>

            {!main && <Balance />}

            <Breadcrumbs className={classes.breadcrumb}>
              <Link
                className={classes.link}
                to="/dashboard/payment-for-services"
              >
                Все услуги
              </Link>
              {name && (
                <Link
                  className={classes.link}
                  to={`/dashboard/payment-for-services/${
                    data && data["@grpId"]
                  }`}
                >
                  {name}
                </Link>
              )}
              <Typography className={classes.link}>
                {data && data["@country"]}
              </Typography>
              <Typography className={classes.link}>
                {data && data["@name"]}
              </Typography>
            </Breadcrumbs>
            {dataContent && (
              <Box className={classes.wrapper}>
                <StepperMain
                  classes={classes}
                  activeStep={activeStep}
                  steps={steps}
                  isStepSkipped={isStepSkipped}
                />

                {isDetailLoading ? (
                  <Reload />
                ) : (
                  <Formik
                    initialValues={{
                      account: "",
                      amountInUSDT: "",
                      amountInKGS: "",
                      comment: "",
                      currency_code: "",
                    }}
                    onSubmit={(values) => {
                      setLoading(true);

                      if (
                        (data["@grpId"] == 1 && data["@country"] == "Россия") ||
                        data["@id"] === "1203" ||
                        data["@id"] === "3107"
                      )
                        values.account = values.account.replace(
                          /[\s+().,-]/g,
                          ""
                        );
                      if (data["@grpId"] == 1 && data["@country"] == "Россия")
                        values.account = values.account.slice(1);
                      else
                        values.account = values.account.replace(
                          /[\s+().,-]/g,
                          ""
                        );

                      let dataToSend = Object.entries(values);

                      if (data["@id"] == 1781) {
                        dispatch(
                          getCheck({
                            data: `<to service='${detailsId}' amount='${
                              values.amountInKGS
                            }' currency='417' commission='5' identification_data='ya' fio_uzcard='Yusupov Javlonbek' fio='Yusupov Javlonbek' payer_dateto='29373637' payer_issuer='Hdhdhdh' payer_passport_num='8363636' payer_inn='7463636' to_fio='Hdhdhdbdb' ${dataToSend
                              .filter(
                                (item) =>
                                  item[0] !== "amountInKGS" &&
                                  item[0] !== "amountInUSDT" &&
                                  item[0] !== "currency_code"
                              )
                              .map((item) => `${item[0]}='${item[1]}'`)
                              .join(" ")} />`,
                          })
                        );
                      } else if (data["@id"] == 1986) {
                        dispatch(
                          getCheck({
                            data: `<to service='${detailsId}' amount='${
                              values.amountInKGS
                            }' currency='417' commission='5' identification_data='12131' fio_uzcard='Yusupov Javlonbek' fio='Yusupov Javlonbek' payer_dateto='ya' payer_issuer='ya' payer_passport_num='ya' payer_inn='ya' to_fio='ya' ${dataToSend
                              .map((item) => `${item[0]}='${item[1]}'`)
                              .join(" ")} />`,
                          })
                        );
                      } else if (data["@grpId"] == 106) {
                        dispatch(
                          getCheck({
                            data: `<to service='${detailsId}' amount='${
                              values.amountInKGS
                            }' currency='417' commission='5' unmasked_phone_number='0999999999' identification_data='ya'  payer_dateto='ya' payer_issuer='ya' payer_passport_num='ya' payer_inn='ya' to_fio='ya' currency_text='KGS' currency_code='KGS'
                             ${dataToSend
                               .filter(
                                 (item) =>
                                   item[0] !== "amountInKGS" &&
                                   item[0] !== "amountInUSDT" &&
                                   item[0] !== "currency_code" &&
                                   item[1] !== "currency_text"
                               )
                               .map((item) => `${item[0]}='${item[1]}'`)
                               .join(" ")} />`,
                          })
                        );
                      } else if (
                        data["@grpId"] == 1 &&
                        data["@country"] == "Россия"
                      ) {
                        dispatch(
                          getCheck({
                            data: `<to service='${detailsId}' amount='${
                              values.amountInKGS
                            }' currency='417'  commission='5' identification_data='Vasya' email='vasya@gmail.com' payer_dateto='Vasya' payer_issuer='Vasya' payer_passport_num='Vasya' payer_inn='Vasya' to_fio='Vasya' ${dataToSend
                              .map((item) => `${item[0]}='${item[1]}'`)
                              .join(" ")} />`,
                          })
                        );
                      } else {
                        dispatch(
                          getCheck({
                            data: `<to service='${detailsId}' amount='${
                              values.amountInKGS
                            }' currency='417' commission='5' ${dataToSend
                              .map((item) => `${item[0]}='${item[1]}'`)
                              .join(" ")} />`,
                          })
                        );
                      }
                    }}
                  >
                    {({ values, handleChange, setFieldValue, errors }) => (
                      <Form className={classes.content}>
                        {activeStep === steps.length ? (
                          <ThirdStep
                            values={values}
                            data={data}
                            t={t}
                            handleReset={handleReset}
                          />
                        ) : (
                          <>
                            {activeStep === 0 && (
                              <FirstStep
                                errors={errors}
                                details={ServicesDetail}
                                values={values}
                                setFieldValue={setFieldValue}
                                handleChange={handleChange}
                                dataContent={dataContent}
                                classes={classes}
                                t={t}
                                data={data}
                              />
                            )}{" "}
                            {activeStep === 1 && (
                              <SecondStep
                                t={t}
                                main={main}
                                setLoading={setLoading}
                                loading={loading}
                                handleNext={handleNext}
                              />
                            )}
                          </>
                        )}
                      </Form>
                    )}
                  </Formik>
                )}
              </Box>
            )}
          </Container>
        </Paper>
      )}
    </>
  );
};

export default Pay24Detail;

const useStyles = makeStyles((theme) => ({
  content: {
    marginTop: 50,
  },

  section: {
    "& .MuiStepper-root ": {
      backgroundColor: "transparent",
      padding: 0,
    },
  },

  formControl: {
    // backgroundColor: "#FFF!important",
    marginBottom: 16,
    // borderRadius: 16,

    "& .MuiInputLabel-formControl": {
      // left: 14,
    },

    "& .MuiInputLabel-shrink": {
      left: 0,
    },
  },

  breadcrumb: {
    marginTop: 40,
    marginBottom: 26,
  },

  link: {
    color: "#000",
    fontSize: 23,
  },

  stepLabel: {
    "& svg": {
      color: "transparent",
      borderRadius: 50,
      width: "85px",
      height: "85px",
      border: "1px solid #E5E5E5",

      [theme.breakpoints.down(768)]: {
        width: 60,
        height: 60,
      },

      "&.Mui-active": {
        color: "#E7EAEE",
        border: "none",
      },

      "&.Mui-completed": {
        color: "#FCE34D",
        border: "none",
      },

      "& text": {
        fill: "#000",
        fontSize: 9,
      },
    },
  },

  input: {
    "& .MuiOutlinedInput-input": {
      backgroundColor: "#fff",
      boxShadow: "0px 1px 0px #E2E8F0",
      borderRadius: 8,
    },
    marginBottom: 16,
  },

  question: {
    position: "absolute",
    top: 2,
    right: 2,
    padding: "0!important",
    minWidth: "unset",
  },

  inputWithBlock: {
    "& .MuiOutlinedInput-input": {
      backgroundColor: "#fff",
      transition: "all 0.2s linear",
      borderRadius: "8px 0 0 8px!important",
    },

    "& .MuiOutlinedInput-notchedOutline": {
      borderRadius: "8px 0 0 8px!important",
    },
  },

  wrapper: {
    width: "60%",

    [theme.breakpoints.down(992)]: {
      width: "100%",
    },
  },

  buttonSubmit: {
    width: "100%",
    height: 56,
  },
}));
